import { request,getToken,Token } from '../../request.js'
export default {
	data() {
		return {
			user_name:'',
		};
	},
	methods: {
		// 修改昵称
		reset() {
			var user_name = this.user_name;
			//验证字符
			request({
				url: "index.php?s=/wap/member/apiModifyNickName",
				method: "POST",
				data: {
					nickname: user_name
				}
			}).then(res => {
				if(res.data.code == 1){
						uni.showToast({
							title: '修改成功',
							icon: 'none'
						});
						
						setTimeout(function(){
							uni.navigateBack({
								delta:1
							});
						}, 1500);
				}
				else {
					uni.showToast({
						title: '修改失败',
						icon: 'none'
					})
				}
			})
		},
		
	}

}
